<template>
  <div class="picbox">
    <ul>
      <li
        class="reveal-bottom"
        v-for="(i,index) in dataScenery"
        :key="index"
      >
        <a href="/">
          <i>
            <img
              :src="getImgSceneryUrl(i.img)"
              @click.stop.prevent="select_photo(i.detailsUrl)"
            />
          </i>
          <div class="picinfo">
            <h3>{{i.title}}</h3>
            <span>{{i.desc}}</span>
          </div>
        </a>
      </li>
    </ul>
    <ul>
      <li
        class="reveal-bottom"
        v-for="(i,index) in dataLove"
        :key="index"
      >
        <a href="/">
          <i>
            <img
              :src="getImgLoveUrl(i.img)"
              @click.stop.prevent="select_photo(i.detailsUrl)"
            />
          </i>
          <div class="picinfo">
            <h3>{{i.title}}</h3>
            <span>{{i.desc}}</span>
          </div>
        </a>
      </li>
    </ul>
    <ul>
      <li
        class="reveal-bottom"
        v-for="(i,index) in  dataAntiquity"
        :key="index"
      >
        <a href="/">
          <i>
            <img
              :src="getImgAntiquityUrl(i.img)"
              @click.stop.prevent="select_photo(i.detailsUrl)"
            />
          </i>
          <div class="picinfo">
            <h3>{{i.title}}</h3>
            <span>{{i.desc}}</span>
          </div>
        </a>
      </li>
    </ul>
    <ul>
      <li class="reveal-bottom" v-for="(i,index) in dataAboretum" :key="index">
        <a href="/">
          <i>
            <img :src="getImgAboretumUrl(i.img)" @click.stop.prevent="select_photo(i.detailsUrl)"/>
          </i>
          <div class="picinfo">
            <h3>{{i.title}}</h3>
            <span>{{i.desc}}</span>
          </div>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
import scrollReveal from "scrollreveal";
export default {
  data() {
    return {
      scrollReveal: scrollReveal(),
      dataScenery: [
        {
          img: "scenery0016",
          title: "我的景物图片",
          desc: "随手拍一些景物留作纪念",
          detailsUrl: "/photo_details_scenery"
        }
      ],
      dataLove: [
        {
          img: "love0005",
          title: "我的那个她",
          desc: "愿你永远18岁",
          detailsUrl: "/photo_details_love"
        }
      ],
      dataAntiquity: [
        {
          img: "antiquity0013",
          title: "古风照片了解一下",
          desc: "是不是很漂亮，最丑的是我",
          detailsUrl: "/photo_details_antiquity"
        }
      ],
      dataAboretum: [
        {
          img: "aboretum0007",
          title: "美丽的植物园",
          desc: "亲近大自然，走进自然风光",
          detailsUrl: "/photo_details_aboretum"
        }
      ]
    };
  },
  mounted() {
    this.scrollReveal.reveal(".reveal-bottom", {
      // 动画的时长
      duration: 1000,
      // 延迟时间
      delay: 500,
      // 动画开始的位置，'bottom', 'left', 'top', 'right'
      origin: "bottom",
      // 回滚的时候是否再次触发动画
      reset: false,
      // 在移动端是否使用动画
      mobile: false,
      // 滚动的距离，单位可以用%，rem等
      distance: "20px",
      // 其他可用的动画效果
      opacity: 0.001,
      easing: "linear",
      scale: 0.9
    });
  },
  methods: {
    select_photo(detailsUrl) {
      this.$router.push(detailsUrl);
    },
    getImgAntiquityUrl(src) {
      return require("@/assets/file/photo_details/antiquity/" + src + ".jpg");
    },
    getImgSceneryUrl(src) {
      return require("@/assets/file/photo_details/scenery/" + src + ".jpg");
    },
    getImgLoveUrl(src) {
      return require("@/assets/file/photo_details/love/" + src + ".jpg");
    },
    getImgAboretumUrl(src) {
      return require("@/assets/file/photo_details/aboretum/" + src + ".jpg");
    }
  }
};
</script>

<style lang="scss" src="@/assets/scss/photo.scss" scoped>
</style>


